import React from 'react';
import Header from './header';
import  PokeList  from './pokemonlist';
import Footer from './footer';

import Fade from '@material-ui/core/Fade';


const App =  () => {
	

	let fad = () =>
	{
		return setTimeout(true , 100000);
	}
  return ( 
	  <div>
		<Header  disable={false}/>

	<Fade in={fad()}>
 		<PokeList />
	 </Fade>
	  	<Footer />
	</div>
  )
}

export default App;